<template>
    <div class="container">
        <div class="header">
            <div class="header-cn">
                出行查询
            </div>
            <div class="header-en">
                Travel Inquiry
            </div>
        </div>
        <div class="input-box">
            <div class="station-input-widget">
                <input @click="handleSelect($event)" class="dir1" type="text" placeholder="输入起始站" v-model="sPoint" maxlength="20">
            </div>
        </div>
        <div class="swap-box">&nbsp;</div>
        <div class="input-box">
            <div class="station-input-widget">
                <input @click="handleSelect($event)" class="dir2" type="text" placeholder="输入终点站" v-model="ePoint" maxlength="20">
            </div>
        </div>
        <div class="button">
            <button>搜索</button>
        </div>
        <div class="left_item">
            <div class="pathOne">
                <span class="one">1号线</span>
                <a @click="handleReturn()" href="javascript:;">返回</a>
                <ul @click="handleUL($event)">
                    <li>双港</li>
                    <li>孔目湖</li>
                    <li>长江路</li>
                    <li>珠江路</li>
                    <li>庐山南大道</li>
                    <li>绿茵路</li>
                    <li>卫东</li>
                    <li>地铁大厦</li>
                    <li>秋水广场</li>
                    <li>滕王阁</li>
                    <li>万寿宫</li>
                    <li>八一馆</li>
                    <li>八一广场</li>
                    <li>丁公路北</li>
                    <li>师大南路</li>
                    <li>彭家桥</li>
                    <li>谢家村</li>
                    <li>青山湖大道</li>
                    <li>高新大道</li>
                    <li>艾溪湖西</li>
                    <li>艾溪湖东</li>
                    <li>太子殿</li>
                    <li>奥体中心</li>
                    <li>瑶湖西</li>
                </ul>
            </div>
            <div class="pathTwo">
                <span class="two">2号线</span>
                <ul @click="handleUL($event)">
                    <li>辛家庵</li>
                    <li>顺外</li>
                    <li>南昌火车站</li>
                    <li>丁公路南</li>
                    <li>永叔路</li>
                    <li>八一广场</li>
                    <li>福州路</li>
                    <li>青山路口</li>
                    <li>阳明公园</li>
                    <li>红谷中大道</li>
                    <li>雅苑路</li>
                    <li>地铁大厦</li>
                    <li>翠苑路</li>
                    <li>学府大道东</li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name : 'Search',
    data() {
        return {
            sPoint : "",
            ePoint : "",
            active : ""
        }
    },
    methods:{
        handleSelect : function(e) {
            var left_item = document.getElementsByClassName('left_item')[0];
            left_item.style.top = '120%';
            left_item.style.opacity = 1;
            left_item.style.display = 'block';
            this.active = e;
            console.log(this.active)
            // var test = function() {
            //     left_item.style.display = 'block';
            // }
            // setTimeout(test,100);
        },
        handleReturn : function() {
            var left_item = document.getElementsByClassName('left_item')[0];
            left_item.style.top = '20%';
            left_item.style.opacity = 0;
            var test = function() {
                left_item.style.display = 'none';
            }
            setTimeout(test,300);
        },
        handleUL : function(e) {
            var target = e.target;
            if(target.nodeName !== 'LI') return;
            if(this.active.srcElement.className == "dir1") {
                this.sPoint = target.innerText;
            } else {
                this.ePoint = target.innerText;
            }
            this.handleReturn();
        }
    }
}
</script>
